import React, { useEffect, useState } from "react";
import { tryGet } from "kit";
import { Empty, Button } from "@arco-design/web-react";
import "./index.less";
import KMapUeContent from "./index";
import SceneDestoryUrl from "@/assets/img/scene-destory.svg";
interface UePreviewProps {
  domId?: string;
  spinClassName?: string;
  className?: string;
  solution?: number;
  autoDestroy?: number;
  children?: React.ReactNode | string;
  onLoad: (res) => void;
  onDestroy?: () => void;
}
const UePreview = (props: UePreviewProps) => {
  const { solution, onLoad, onDestroy, autoDestroy = 0 } = props;
  const [ueError, setUeError] = useState(null);
  const [isDestroy, setIsDestroy] = useState(false);
  const [ueResult, setUeResult] = useState(true);
  const [ueObj, setUeObj] = useState(null);
  const [isRefresh, setIsRefresh] = useState(true);
  return (
    <div className="ue-preview-box">
      {solution && ueResult && !isDestroy && (
        <KMapUeContent
          solution={Number(solution)}
          autoDestroy={autoDestroy}
          onLoad={(res) => {
            onLoad && onLoad(res);
            if (res.code === 200) {
              setUeResult(true);
              setUeObj(res.ue);
              res.ue?.on("error", function (data) {
                setUeResult(false);
                setUeError({
                  res: { code: data.code, message: data.msg },
                  code: 500,
                });
              });
            } else {
              if (tryGet(res, "res.code") === "S199315") {
                setIsRefresh(false);
              } else {
                setIsRefresh(true);
              }
              setUeResult(false);
              setUeError(res);
            }
          }}
          onDestroy={() => {
            setIsDestroy(true);
            onDestroy && onDestroy();
          }}
        />
      )}
      {ueError && !isDestroy && (
        <Empty
          imgSrc={require("@/assets/img/no-idle-instances.png")}
          description={
            <div>
              <div
                className="no-label"
                style={{
                  color: "rgba(255, 255, 255, 0.88)",
                  fontSize: 18,
                  padding: "30px 0",
                }}
              >
                {/* 当前场景暂无空闲实例资源，请稍后重试 */}
                {tryGet(ueError, "res.message")}
              </div>
              {isRefresh && (
                <Button
                  type="primary"
                  onClick={() => {
                    setUeResult(true);
                    setUeError(null);
                    setIsDestroy(false);
                  }}
                >
                  刷新
                </Button>
              )}
            </div>
          }
        />
      )}
      {isDestroy && (
        <Empty
          imgSrc={SceneDestoryUrl}
          description={
            <div>
              <div
                className="no-label"
                style={{
                  color: "rgba(255, 255, 255, 0.88)",
                  fontSize: 18,
                  padding: "30px 0",
                }}
              >
                长时间未操作，场景服务已断开链接，请点击
                <span
                  className="animated-underline"
                  onClick={() => {
                    setUeResult(true);
                    setUeError(null);
                    setIsDestroy(false);
                  }}
                >
                  重新加载
                </span>
                ，实例化场景。
              </div>
            </div>
          }
        />
      )}
    </div>
  );
};
export default UePreview;
