import React, { useState, useEffect } from "react";
import { observer } from "mobx-react";
// import AppStore from '../../store';
import { UePreview } from "@/components";
import { Button } from "@arco-design/web-react";
import "./index.less";
import titleImg from "@/assets/img/title.png";
// import footerImg from "@/assets/img/footer.png";
import leftInfoImg from "@/assets/img/left-info.png";
import rightInfoImg from "@/assets/img/right-info.png";
import ydzl from "@/assets/img/nav/ydzl.svg";
import ydzlHover from "@/assets/img/nav/ydzl-hover.svg";
import gll from "@/assets/img/nav/gll.svg";
import gllHover from "@/assets/img/nav/gll-hover.svg";
import yhl from "@/assets/img/nav/yhl.svg";
import yhlHover from "@/assets/img/nav/yhl-hover.svg";
import gcl from "@/assets/img/nav/gcl.svg";
import gclHover from "@/assets/img/nav/gcl-hover.svg";
import gcsx from "@/assets/img/nav/gcsx.svg";
import gcsxHover from "@/assets/img/nav/gcsx-hover.svg";
import ldcl from "@/assets/img/nav/ldcl.svg";
import ldclHover from "@/assets/img/nav/ldcl-hover.svg";
import xhty from "@/assets/img/nav/xhty.svg";
import xhtyHover from "@/assets/img/nav/xhty-hover.svg";
import arrow from "@/assets/img/arrow.svg";
import dot from "@/assets/img/dot.svg";
import topInfo from "@/assets/img/top-info.png";
const baseUrl = window.location.origin;
const navList = [
  {
    label: "拥堵治理",
    value: "ydzl",
    icon: ydzl,
    iconHover: ydzlHover,
    children: [
      {
        label: "重点路段疏堵策略",
        value: "zdld",
        href: "http://36.133.245.96:60505/tzvisual/dist/#/traffic?token=eyJraWQiOiIxNzMwMzU4NDQ1NDM0IiwidHlwIjoiSldUIiwiYWxnIjoiSFMyNTYifQ.eyJzdWIiOiJhZG1pbiIsImV4cCI6MTczNTU3NDQwMCwiaWF0IjoxNzMwMzAwNDAwfQ.PMVq0rZANAEnWAfAzCKGFGj5KqczxAOJSpHjCXOX6wM",
      },
      {
        label: "重点区域疏堵策略",
        value: "zdqy",
        href: "http://36.133.245.96:60505/tzvisual/dist/#/trafficarea",
      },
      {
        label: "公共交通线路优化",
        value: "jtyh",
        href: "http://36.133.245.96:60505/tzvisual/dist/#/bus",
      },
      {
        label: "重大项目优化分析",
        value: "zdxm",
        href: "http://203.0.105.238:8072/#/majorProject/projectList",
      },
    ],
  },
  {
    label: "信号调优",
    value: "xhty",
    icon: xhty,
    iconHover: xhtyHover,
    children: [],
  },
  {
    label: "工程时序",
    value: "gcsx",
    icon: gcsx,
    iconHover: gcsxHover,
    children: [],
  },
  {
    label: "联动处置",
    value: "ldcz",
    icon: ldcl,
    iconHover: ldclHover,
    children: [],
  },
  {
    label: "管理类",
    value: "gll",
    icon: gll,
    iconHover: gllHover,
    children: [
      {
        label: "流量统计",
        value: "lltj",
        href: `${baseUrl}/#/manage/lltj`,
      },
      {
        label: "事件统计",
        value: "sjtj",
        href: `${baseUrl}/#/manage/sjtj`,
      },
      {
        label: "视频调阅",
        value: "spdy",
        href: `${baseUrl}/#/manage/spdy`,
      },
      {
        label: "路口评价",
        value: "lkpj",
        href: `${baseUrl}/#/manage/lkpj`,
      },
      {
        label: "车辆跟踪",
        value: "clgz",
        href: `${baseUrl}/#/manage/clgz`,
      },
    ],
  },
  {
    label: "优化类",
    value: "yhl",
    icon: yhl,
    iconHover: yhlHover,
    children: [
      {
        label: "信号调整",
        value: "xhtz",
        href: `${baseUrl}/#/optimize/xhtz`,
      },
      {
        label: "路口渠化",
        value: "lkqh",
        href: `${baseUrl}/#/optimize/lkqh`,
      },
      {
        label: "交通诱导",
        value: "jtyd",
        href: `${baseUrl}/#/optimize/jtyd`,
      },
      {
        label: "公交线路",
        value: "gjxl",
        href: `${baseUrl}/#/optimize/gjxl`,
      },
    ],
  },
  {
    label: "工程类",
    value: "gcl",
    icon: gcl,
    iconHover: gclHover,
    children: [
      {
        label: "新增道路",
        value: "xzdl",
        href: `${baseUrl}/#/optimize/gjxl`,
      },
      {
        label: "隔离带调整",
        value: "gldtz",
        href: `${baseUrl}/#/optimize/gjxl`,
      },
      {
        label: "公交车改造",
        value: "gjcgz",
        href: `${baseUrl}/#/optimize/gjxl`,
      },
    ],
  },
];
let viewer = null;
const Home = () => {
  const [active, setActive] = useState("ydzl");
  const [child, setChild] = useState("");
  const [childList, setChildList] = useState(navList[0].children);
  useEffect(() => {}, []);
  const { solution } = window["ueConfig"];
  return (
    <div className="home-box">
      <div className="header-wrap">
        <img src={titleImg} alt="" />
      </div>
      <div className="top-wrap">
        <img src={topInfo} alt="" />
      </div>
      <div className="left-mask"></div>
      <div className="left-side">
        <img src={leftInfoImg} alt="" />
      </div>
      <div className="right-mask"></div>
      <div className="right-side">
        <img src={rightInfoImg} alt="" />
      </div>
      <div className="footer-mask"></div>
      <div className="footer-wrap">
        <Button
          type="primary"
          className={"anbao"}
          onClick={() => {
            const geometryUtil = new window["KMapUE"].GeometryUtil({ viewer });
            geometryUtil.draw({
              type: window["KMapUE"].GeometryType.POLYLINE,
              onComplete: (geometry) => {
                console.log(geometry, "几何图形实体对象");
                // geometry：几何图形实体对象
              },
            });
          }}
        >
          绘制
        </Button>
        <Button
          type="primary"
          className={"sprh"}
          onClick={async () => {
            const cameraInfo = await viewer.getCameraInfo();
            console.log(cameraInfo, "cameraInfocameraInfocameraInfo");

            viewer.flyTo({
              ...cameraInfo,
              fov: 90,
              lng: 116.6376121516001,
              lat: 39.90631665290631,
              alt: 211.22539188799973,
              x: -783540.5858521967,
              y: -1416179.1314937503,
              z: 21122.539188799972,
              pitch: -61.65714645385742,
              heading: 178.4000701904297,
              roll: 4.689634843109934e-14,
            });
            // window.open(
            //   "https://gt5-test.testdolphin.com/kmeta-security-frontend/#/security_platform",
            //   "_blank"
            // );
          }}
        >
          万达
        </Button>
        <div className="nav-wrap">
          {navList.map((item) => (
            <div
              className={`nav ${active == item.value ? "active" : ""}`}
              key={item.value}
              onClick={() => {
                setActive(item.value);
                setChildList(item.children);
              }}
            >
              <div className="nav-con">
                <img src={item.icon} className="pic" alt="" />
                <img src={item.iconHover} className="pic-hover" alt="" />
                <div className="tit">{item.label}</div>
              </div>
            </div>
          ))}
          <div className="nav-child-wrap">
            {childList.map((item) => (
              <div
                className={`nav-child-li ${
                  child == item.value ? "active" : ""
                }`}
                key={item.value}
                onClick={() => {
                  setChild(item.value);
                  window.open(item.href, "_blank");
                }}
              >
                {child == item.value && (
                  <img src={arrow} className="arrow arrow-left" />
                )}
                {child != item.value && <img src={dot} width={20} />}
                <label htmlFor="">{item.label}</label>
                {child == item.value && (
                  <img src={arrow} className="arrow arrow-right" />
                )}
              </div>
            ))}
          </div>
        </div>
      </div>
      {solution && (
        <UePreview
          solution={solution}
          onLoad={(res) => {
            if (res.code === 200) {
              viewer = res.ue;
              viewer.setTime(1730304000000);

              viewer.setWeather({
                weather: window["KMapUE"].WEATHER.SUNNEY,
              });
            }
          }}
        />
      )}
    </div>
  );
};
export default observer(Home);
